







import Vue from 'vue'
import { Organization, Drop, ScheduleLocation } from '@/types/interface/index.d'
import { getMockData } from '@/api/http'

export default Vue.extend({
  name: 'Quoting' as string,
  created() {
    getMockData().then((response) => {
      this.errorFlag = false
      this.organization = response.organization
      this.drop = response.drop
      this.schedule = response.schedule
    })
  },
  data() {
    return {
      loading: true,
      errorFlag: false,
      organization: {} as Organization,
      drop: {} as Drop,
      schedule: [] as ScheduleLocation[],
    }
  },
})
